<template>
    <div class="" style="overflow: auto;" @scroll="scrollEvent">
        <div class="w90p p20" style="margin:0 auto;">
            <div class="f18 clof p5_0 ">当日成交统计</div>
            <!-- <div class="secondMenus mt24" style="align-items: center;">市场：
                <div v-for="(v,o) in areaList" :key="o" class="secondMenu" :class="{'active':active==o}" @click="active=o">{{v.text}}</div>
            </div> -->
            <!-- <div class="mt10">
                发布时间：
                <DatePicker type="date" placeholder="请选择" v-model="page.commitDate" @on-change="search"></DatePicker>
                <span class="ml20">意向产品：</span>
                <Select v-model="page.varietyId" @on-change="search" class="w200" placeholder="请选择" clearable>
                    <Option value="beijing">供</Option>
                    <Option value="shanghai">需</Option>
                </Select>
                <span class="ml20">供需方向：</span>
                <Select v-model="page.direction" @on-change="search" class="w200" placeholder="请选择" clearable>
                    <Option value="买">供</Option>
                    <Option value="卖">需</Option>
                </Select>
                <Button shape="circle" class="ml10 right" style="min-width:80px;">条件选品种</Button>
            </div> -->
            <table class="defineTable mt20 dataList " cellspacing="0">
                <thead>
                    <tr class="">
                        <td class="">#</td>
                        <td class="tleft">发布时间</td>
                        <td class="">意向品种</td>
                        <td class="">供需方向</td>
                        <td>意向数量</td>
                        <td>意向价格(元)</td>
                        <td>联系人</td>
                        <td>手机号</td>
                        <td>有效期</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(v,o) in list" :key="o">
                        <td class="">{{ o+1 }}</td>
                        <td class="tleft">{{ v.commitDate }}</td>
                        <td class="">{{v.varietyId}}</td><!--福建省碳排放量-->
                        <td>
                            <span v-if="v.direction=='买'" class="red">买入</span>
                            <span v-else-if="v.direction=='卖'" class="green">卖出</span>
                        </td><!--B buy  S sell-->
                        <td>{{ v.vol | numFormat(2) }}</td>
                        <td>{{ v.price | numFormat(2) }}</td>
                        <td>{{ v.contractName }}</td>
                        <td class="">{{ v.phone }}</td>
                        <td class="">{{v.expireDate}}</td><!--1个月-->
                    </tr>
                    <tr v-if="list.length==0">
                        <td colspan="9" class="center cloc">暂无数据</td>
                    </tr>
                    <!-- <tr v-for="(v,o) in 10" :key="o">
                        <td class="tleft">FQ25865-全国交易所TESTL</td>
                        <td>45.5</td>
                        <td>50.5</td>
                        <td>46.5</td>
                        <td class="green">-1.5%</td>
                        <td class="green">15000</td>
                        <td class="">877621.15</td>
                        <td class="">1%</td>
                    </tr> -->
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
export default {
    name: "",
    props: {},
    components: {},
    data() {
        return {
            active:0,
            areaList:this.$store.state.app.areaList,//区域列表
            list:[],
            page:{
                "current": 1,
                "size": 20,
                loading:false,
                finish:false,
            },
        };
    },
    mounted() {
        this.getList();
    },
    methods: {
        search(){
            this.page.current=1;
            this.getList();
        },
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        //列表
        async getList(){
            if(this.page.loading){
                return;
            }
            // china：中国
            // other：国外
            // eu：其他
            let d={
                // area: this.areaList[this.active].type,
                // area:'shenzhen',//只有深圳有数据
                // commitDate:this.page.commitDate&&this.$dayjs(this.page.commitDate).format('YYYY-MM-DD HH:mm:ss'),
                // varietyId:this.page.varietyId,
                // direction:this.page.direction,
                // area: 'HU_BEI',
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading=true;
            // let res = await this.$api.order_actual_page(d);
            let res = await this.$api.trade_day_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.list=list;
            }else{
                this.list=this.list.concat(list);
            }
            this.page.loading=false;
        },
        toTrading(){
            this.$router.push('/trading?');
        }
    },
};
</script>

<style scoped lang='less'>
.defineTable{
    background: #17171a;
}
</style>